@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;900&display=swap);




/* Project Thumbnail */
.project-img {
    width: 100%;
    height: 100%;
    border-radius: 18px;
    transition: ease .5s;
    z-index: 3000;
}

/* Meta container */
.meta-container {
    background: #007CBA;
    height: 252px;
    border-radius: 18px;
    z-index: 1;
    padding: 125px 30px 0;
    margin-bottom: 7rem;
}

.project-links {
    margin-bottom: 25px;
    display: flex;
    justify-content: space-between;

}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45%;
    background-color: #076492;
    padding: 10px;
    border-radius: 23px;
    color: aliceblue;
    font-weight: 600;
    transition: ease .7s;
}

.button:hover {
    color: #076492;
    background-color: aliceblue;
    transition: ease .7s;
}

.button h5 {
    margin-top: 7px;
    margin-left: 5px;
}



/* Animation Classes */
.cardSlide {
    position: relative;
    top: 178px;
    z-index: 2;
    transition: ease .5s;
    color: none;
    padding: 0;
    border-radius: 23px;
    border: none;
    display: inline-block;
    box-shadow: 0px 4px 23px 0px rgba(0,0,0,0.65);
}

.cardSlide:hover {
    box-shadow: 0px 10px 30px 0px rgba(0,0,0,0.75);
    transition: ease .5s;
}

.cardSlide:focus {
    border: none;
    outline: none;
}

.cardSlideUp {
    top: 90px;
    z-index: 2;
    transition: ease .5s;
}

.section-margin {
    margin-top: 8rem;
}
.design__contain {
    padding-top: 200px;
    display: flex;
    justify-content: center;
}

.design-projects-container {
    margin-top: -170px;
}

.design__icon {
    margin-top: 3px;
    font-size: 20px;
    
}

.design__text {
    font-size: 20px;
    margin-right: 15px;
}

@media only screen and (max-width: 767px) {
    .designCardSlide {
        top: 104px;
    }

    .design__contain {
        padding-top: 125px;
        height: 170px;
    }

    .design-projects-container {
        margin-top: -90px;
    }

}


.hidden {
    display: none;
}

.form-control {
    background-color: #353535;
}

.btn-primary, .btn {
    border-color: none;
    border: none;
}


.nav__logo-desktop {
    margin-right: 65px;
    height: 40px;
}

.nav__logo-mobile {
    height: 32px;
}

/* Many Navbar Overrides Are Located on the "global.css" files To Solve Specificity */

.nav__social {
    font-size: 17px;
}

/* Toggle Button */
.navbar-light .navbar-toggler {
    background: none;
}


img {
    height: 32px;
}

footer {
    padding: 30px 20px;
    box-shadow: inset 0px 8px 20px 5px rgba(0, 0, 0, 0.4);
    position:relative;
    left:0;
    bottom:0;
    right:0;
    margin-top: 10rem;
}


.resume {
    margin-top: 5px;
    text-decoration: underline;
}

.social {
    margin-left: 16px;
    color: white;
    transition: ease .5s;
} 

.social:focus, 
.social:hover,
.resume:hover,
.resume:focus{
    color: white;
    opacity: .5;
    transition: ease .5s;
  }
  
  .social:active,
  .social:visited,
  .resume:active,
  .resume:visited {
    color: white;
    transition: ease .5s;
  }


body {
  margin: 0;
  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #202120;
  font-weight: 600;
  /* height: 100%; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  text-align: center;
  padding: 130px 0;
}


/* Fonts */
h1 {
  font-weight: 900;
  font-size: 36px;
  line-height: 140%;
  max-width: 350px;
  margin: 0 auto;
}

h2 {
  font-weight: 900;
  font-size: 180px;
  line-height: 140%;
  text-align: center;
  color: #353535;
  margin-top: -5rem;
}

h3 {
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  max-width: 210px;
  margin: 0 auto;
}

h4 {
  font-weight: 900;
  font-size: 16px;
}

h5 {
  font-weight: 400;
  font-size: 14px;
  line-height: 70%;
}

h6 {
  font-weight: 600;
  font-size: 16px;
  margin: 0;
}

p {
  font-weight: 400;
  font-size: 22px;
  line-height: 140%;
}

.txt-bold {
  font-weight: 900;
}



/* Colors */
.bg-color {
  background: #202120;
}
.primary-blue-color {
  background: #545657;
}
.secondary-blue-color {
  background: #00628A;
}
.section-headlines-color {
  background: #353535;
}
.white-color {
  color: white;
}


/* Form */
.form-control {
  background-color: #353535;
  border: 1px #202020 solid;
  margin-bottom: 30px;
  height: 50px;
  padding: 15px;
}

.form-control:focus {
  background-color: rgb(29, 29, 29);
  color: rgb(223, 223, 223);
  border-color: #00628A;
}

label {
  color: #bac2c5;
}


/* Line Block */
hr {
  border-top: solid #007CBA 9px;
  width: 210px;  
}

.btn-primary {
  background-color: #00628A;
  width: 130px;
}


/* Flex */
.justify-end {
  justify-content: flex-end;    
}


/* Navbar Overides */
.navbar-light .navbar-nav .nav-link{
  color: white;
  transition: ease .5s;
}

.navbar-light .navbar-nav .nav-link:focus, 
.navbar-light .navbar-nav .nav-link:hover{
  color: white;
  opacity: .5;
  transition: ease .5s;
}

.navbar-light .navbar-nav .nav-link:active,
.navbar-light .navbar-nav .nav-link:visited{
  color: white;
  transition: ease .5s;
}

.navbar-light .navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, .9%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}


/* Page Margins */
.negative-margin {
  margin-top: -6.5rem;
  margin-right: auto;
  margin-left: auto;
}

.projects-container {
    margin-top: -170px;
}


/* MOBILE */
@media only screen and (max-width: 1201px) {
  
  h2 {
    font-size: 155px;
  }

  .negative-margin {
    margin-top: -5rem;
    transition: ease 1s;
  }

  
}

@media only screen and (max-width: 993px) {
  
  h2 {
    font-size: 110px;
  }

  .negative-margin {
    margin-top: -4rem;
    transition: ease 1s;
  }

  .cardSlide,
  .project-img,
  .meta-container {
      border-radius: 30px;
  }

}

@media only screen and (max-width: 767px) {
  .navbar-collapse {
    margin-top: 10px;
    background: #353535;
    padding: 10px;
    text-align: center;
  }

  header {
    padding: 50px 0;
  }
  
  h2 {
    font-size: 70px;
    line-height: 90%;
    margin-top: 2rem;
  }

  .negative-margin {
    margin-top: -1.5rem;
    transition: ease 1s;
  }


  .project-img,
  .meta-container {
      border-radius: 20px;
  }


  nav,
  .social {
    font-size: 18px;
  }

  .nav__social {
    font-size: 19px;
  }
}

@media only screen and (max-width: 439px) {
  h2 {
    font-size: 50px;
    line-height: 90%;
    margin-top: 2rem;
  }


}


